import * as Sentry from '@sentry/react';
import {
  useLocation,
  useNavigationType,
  createRoutesFromChildren,
  matchRoutes,
} from 'react-router-dom';
import React from 'react';

// Cu hnh Sentry cho error monitoring v performance tracking
export const initSentry = () => {
  // Ch khi to Sentry trong production hoc khi c DSN
  const dsn = import.meta.env.VITE_SENTRY_DSN;
  
  if (!dsn && import.meta.env.PROD) {
    console.warn('Sentry DSN khng c cu hnh trong production');
    return;
  }

  Sentry.init({
    dsn: dsn,
    environment: import.meta.env.MODE,
    
    // Tch hp performance monitoring
    integrations: [
      Sentry.browserTracingIntegration(),
      Sentry.replayIntegration({
        maskAllText: false,
        blockAllMedia: false,
      }),
    ],

    // Performance Monitoring
    tracesSampleRate: import.meta.env.PROD ? 0.1 : 1.0, // 10% trong production, 100% trong development
    
    // Session Replay
    replaysSessionSampleRate: 0.1, // 10% ca sessions
    replaysOnErrorSampleRate: 1.0, // 100% khi c li

    // Cu hnh release tracking
    release: import.meta.env.VITE_APP_VERSION || '1.0.0',

    // Lc li khng cn thit
    beforeSend(event, hint) {
      // B qua cc li t extensions hoc scripts bn th ba
      if (event.exception) {
        const error = hint.originalException;
        if (error && error.stack) {
          // B qua li t browser extensions
          if (error.stack.includes('extension://') || 
              error.stack.includes('chrome-extension://') ||
              error.stack.includes('moz-extension://')) {
            return null;
          }
          
          // B qua li network thng thng
          if (error.message && (
            error.message.includes('Network Error') ||
            error.message.includes('Failed to fetch') ||
            error.message.includes('Load failed')
          )) {
            return null;
          }
        }
      }
      
      return event;
    },

    // Cu hnh user context
    initialScope: {
      tags: {
        component: 'yapee-vietnam-clone',
        platform: 'web'
      }
    }
  });

  // Log khi to thnh cng
  console.log('Sentry  c khi to thnh cng');
};

// Helper functions  s dng Sentry
export const captureError = (error: Error, context?: Record<string, any>) => {
  Sentry.withScope((scope) => {
    if (context) {
      Object.keys(context).forEach(key => {
        scope.setContext(key, context[key]);
      });
    }
    Sentry.captureException(error);
  });
};

export const captureMessage = (message: string, level: Sentry.SeverityLevel = 'info', context?: Record<string, any>) => {
  Sentry.withScope((scope) => {
    if (context) {
      Object.keys(context).forEach(key => {
        scope.setContext(key, context[key]);
      });
    }
    Sentry.captureMessage(message, level);
  });
};

export const setUserContext = (user: { id: string; email?: string; username?: string }) => {
  Sentry.setUser(user);
};

export const addBreadcrumb = (message: string, category: string, level: Sentry.SeverityLevel = 'info') => {
  Sentry.addBreadcrumb({
    message,
    category,
    level,
    timestamp: Date.now() / 1000
  });
};

// React Error Boundary vi Sentry
export const SentryErrorBoundary = Sentry.withErrorBoundary;

// HOC  wrap components vi Sentry profiling
export const withSentryProfiling = Sentry.withProfiler;

export { Sentry };