/**
 * Service qun l Progressive Web App (PWA)
 */

interface BeforeInstallPromptEvent extends Event {
  readonly platforms: string[];
  readonly userChoice: Promise<{
    outcome: 'accepted' | 'dismissed';
    platform: string;
  }>;
  prompt(): Promise<void>;
}

class PWAService {
  private deferredPrompt: BeforeInstallPromptEvent | null = null;
  private isInstallable = false;
  private isInstalled = false;
  private installCallbacks: Array<(canInstall: boolean) => void> = [];

  constructor() {
    this.init();
  }

  /**
   * Khi to PWA service
   */
  private init(): void {
    // Kim tra xem app  c ci t cha
    this.checkIfInstalled();

    // Lng nghe s kin beforeinstallprompt
    window.addEventListener('beforeinstallprompt', (e) => {
      e.preventDefault();
      this.deferredPrompt = e as BeforeInstallPromptEvent;
      this.isInstallable = true;
      this.notifyInstallCallbacks(true);
    });

    // Lng nghe s kin appinstalled
    window.addEventListener('appinstalled', () => {
      this.isInstalled = true;
      this.isInstallable = false;
      this.deferredPrompt = null;
      this.notifyInstallCallbacks(false);
      console.log('PWA  c ci t thnh cng');
    });

    // Kim tra nu ang chy trong standalone mode
    if (window.matchMedia('(display-mode: standalone)').matches) {
      this.isInstalled = true;
    }
  }

  /**
   * Kim tra xem app c th ci t khng
   */
  public canInstall(): boolean {
    return this.isInstallable && !this.isInstalled;
  }

  /**
   * Kim tra xem app  c ci t cha
   */
  public isAppInstalled(): boolean {
    return this.isInstalled;
  }

  /**
   * Hin th prompt ci t PWA
   */
  public async showInstallPrompt(): Promise<boolean> {
    if (!this.deferredPrompt) {
      console.warn('Install prompt khng kh dng');
      return false;
    }

    try {
      // Hin th install prompt
      await this.deferredPrompt.prompt();
      
      // Ch ngi dng phn hi
      const { outcome } = await this.deferredPrompt.userChoice;
      
      if (outcome === 'accepted') {
        console.log('Ngi dng  chp nhn ci t PWA');
        return true;
      } else {
        console.log('Ngi dng  t chi ci t PWA');
        return false;
      }
    } catch (error) {
      console.error('Li khi hin th install prompt:', error);
      return false;
    } finally {
      this.deferredPrompt = null;
      this.isInstallable = false;
      this.notifyInstallCallbacks(false);
    }
  }

  /**
   * ng k callback khi trng thi install thay i
   */
  public onInstallStateChange(callback: (canInstall: boolean) => void): () => void {
    this.installCallbacks.push(callback);
    
    // Gi callback ngay lp tc vi trng thi hin ti
    callback(this.canInstall());
    
    // Tr v function  hy ng k
    return () => {
      const index = this.installCallbacks.indexOf(callback);
      if (index > -1) {
        this.installCallbacks.splice(index, 1);
      }
    };
  }

  /**
   * Thng bo cho tt c callbacks v thay i trng thi
   */
  private notifyInstallCallbacks(canInstall: boolean): void {
    this.installCallbacks.forEach(callback => callback(canInstall));
  }

  /**
   * Kim tra xem app  c ci t cha
   */
  private checkIfInstalled(): void {
    // Kim tra standalone mode
    if (window.matchMedia('(display-mode: standalone)').matches) {
      this.isInstalled = true;
      return;
    }

    // Kim tra iOS Safari
    if ((window.navigator as any).standalone === true) {
      this.isInstalled = true;
      return;
    }

    // Kim tra Android Chrome
    if (window.matchMedia('(display-mode: minimal-ui)').matches) {
      this.isInstalled = true;
      return;
    }
  }

  /**
   * Ly thng tin v kh nng PWA ca trnh duyt
   */
  public getBrowserSupport(): {
    serviceWorker: boolean;
    manifest: boolean;
    installPrompt: boolean;
  } {
    return {
      serviceWorker: 'serviceWorker' in navigator,
      manifest: 'manifest' in document.createElement('link'),
      installPrompt: 'BeforeInstallPromptEvent' in window
    };
  }

  /**
   * ng k Service Worker
   */
  public async registerServiceWorker(): Promise<boolean> {
    if (!('serviceWorker' in navigator)) {
      console.warn('Service Worker khng c h tr');
      return false;
    }

    try {
      const registration = await navigator.serviceWorker.register('/sw.js');
      console.log('Service Worker  c ng k:', registration);
      
      // Lng nghe cp nht
      registration.addEventListener('updatefound', () => {
        const newWorker = registration.installing;
        if (newWorker) {
          newWorker.addEventListener('statechange', () => {
            if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
              // C bn cp nht mi
              this.notifyUpdate();
            }
          });
        }
      });
      
      return true;
    } catch (error) {
      console.error('Li khi ng k Service Worker:', error);
      return false;
    }
  }

  /**
   * Thng bo c bn cp nht mi
   */
  private notifyUpdate(): void {
    // C th hin th notification hoc banner
    console.log('C bn cp nht mi cho ng dng');
    
    // Dispatch custom event
    window.dispatchEvent(new CustomEvent('pwa-update-available'));
  }

  /**
   * Reload  p dng bn cp nht mi
   */
  public async applyUpdate(): Promise<void> {
    if ('serviceWorker' in navigator) {
      const registration = await navigator.serviceWorker.getRegistration();
      if (registration && registration.waiting) {
        registration.waiting.postMessage({ type: 'SKIP_WAITING' });
        window.location.reload();
      }
    }
  }

  /**
   * Ly thng tin v kt ni mng
   */
  public getNetworkInfo(): {
    online: boolean;
    effectiveType?: string;
    downlink?: number;
    rtt?: number;
  } {
    const connection = (navigator as any).connection || (navigator as any).mozConnection || (navigator as any).webkitConnection;
    
    return {
      online: navigator.onLine,
      effectiveType: connection?.effectiveType,
      downlink: connection?.downlink,
      rtt: connection?.rtt
    };
  }

  /**
   * Lng nghe thay i trng thi mng
   */
  public onNetworkChange(callback: (networkInfo: { online: boolean; effectiveType?: string; downlink?: number; rtt?: number; }) => void): () => void {
    const handleOnline = () => callback(this.getNetworkInfo());
    const handleOffline = () => callback(this.getNetworkInfo());
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    // Gi callback vi trng thi hin ti
    callback(this.getNetworkInfo());
    
    // Tr v function  hy ng k
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }
}

// Export singleton instance
export const pwaService = new PWAService();
export default pwaService;