import React, { useState, useEffect } from 'react';
import { Wifi, WifiOff, Signal, AlertTriangle } from 'lucide-react';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Badge } from '@/components/ui/badge';
import { pwaService } from '@/services/pwaService';

interface NetworkInfo {
  online: boolean;
  effectiveType?: string;
  downlink?: number;
  rtt?: number;
}

/**
 * Component hin th trng thi kt ni mng
 */
const NetworkStatus: React.FC = () => {
  const [networkInfo, setNetworkInfo] = useState<NetworkInfo>({ online: navigator.onLine });
  const [showOfflineAlert, setShowOfflineAlert] = useState(false);
  const [wasOffline, setWasOffline] = useState(false);

  useEffect(() => {
    // Ly thng tin mng ban u
    const initialNetworkInfo = pwaService.getNetworkInfo();
    setNetworkInfo(initialNetworkInfo);

    // ng k lng nghe thay i trng thi mng
    const unsubscribe = pwaService.onNetworkChange((info) => {
      setNetworkInfo(info);
      
      // Hin th alert khi mt kt ni
      if (!info.online) {
        setShowOfflineAlert(true);
        setWasOffline(true);
      } else {
        // n alert khi c kt ni tr li
        if (wasOffline) {
          setShowOfflineAlert(false);
          // Hin th thng bo kt ni li trong 3 giy
          setTimeout(() => {
            setWasOffline(false);
          }, 3000);
        }
      }
    });

    return unsubscribe;
  }, [wasOffline]);

  /**
   * Ly mu sc da trn cht lng kt ni
   */
  const getConnectionQuality = () => {
    if (!networkInfo.online) return { color: 'destructive', text: 'Offline' };
    
    const { effectiveType, downlink } = networkInfo;
    
    if (effectiveType === '4g' || (downlink && downlink > 1.5)) {
      return { color: 'default', text: 'Tt' };
    } else if (effectiveType === '3g' || (downlink && downlink > 0.5)) {
      return { color: 'secondary', text: 'Trung bnh' };
    } else {
      return { color: 'destructive', text: 'Chm' };
    }
  };

  /**
   * Ly icon ph hp
   */
  const getNetworkIcon = () => {
    if (!networkInfo.online) {
      return <WifiOff className="w-4 h-4" />;
    }
    
    const quality = getConnectionQuality();
    if (quality.color === 'destructive') {
      return <AlertTriangle className="w-4 h-4" />;
    }
    
    return <Wifi className="w-4 h-4" />;
  };

  const quality = getConnectionQuality();

  return (
    <>
      {/* Offline Alert */}
      {showOfflineAlert && !networkInfo.online && (
        <div className="fixed top-0 left-0 right-0 z-50">
          <Alert className="rounded-none border-red-200 bg-red-50 border-b">
            <WifiOff className="w-4 h-4" />
            <AlertDescription className="text-red-800">
              <strong>Khng c kt ni internet.</strong> Mt s tnh nng c th khng hot ng.
            </AlertDescription>
          </Alert>
        </div>
      )}

      {/* Reconnected Alert */}
      {wasOffline && networkInfo.online && (
        <div className="fixed top-0 left-0 right-0 z-50">
          <Alert className="rounded-none border-green-200 bg-green-50 border-b">
            <Wifi className="w-4 h-4" />
            <AlertDescription className="text-green-800">
              <strong> kt ni li internet.</strong> Tt c tnh nng  hot ng bnh thng.
            </AlertDescription>
          </Alert>
        </div>
      )}




    </>
  );
};

export default NetworkStatus;