import React, { useState, useEffect } from 'react';
import { Download, X, Smartphone } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { pwaService } from '@/services/pwaService';
import { useToast } from '@/hooks/use-toast';

/**
 * Component hin th banner/button ci t PWA
 */
const InstallPWA: React.FC = () => {
  const [canInstall, setCanInstall] = useState(false);
  const [showBanner, setShowBanner] = useState(false);
  const [isInstalling, setIsInstalling] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    // ng k lng nghe thay i trng thi install
    const unsubscribe = pwaService.onInstallStateChange((installable) => {
      setCanInstall(installable);
      
      // Hin th banner sau 3 giy nu c th ci t
      if (installable) {
        const timer = setTimeout(() => {
          setShowBanner(true);
        }, 3000);
        
        return () => clearTimeout(timer);
      }
    });

    return unsubscribe;
  }, []);

  /**
   * X l ci t PWA
   */
  const handleInstall = async () => {
    setIsInstalling(true);
    
    try {
      const success = await pwaService.showInstallPrompt();
      
      if (success) {
        toast({
          title: "Ci t thnh cng!",
          description: "ng dng Yapee  c ci t trn thit b ca bn.",
        });
        setShowBanner(false);
      } else {
        toast({
          title: "Ci t b hy",
          description: "Bn c th ci t ng dng bt c lc no t menu trnh duyt.",
          variant: "destructive"
        });
      }
    } catch (error) {
      console.error('Li khi ci t PWA:', error);
      toast({
        title: "Li ci t",
        description: "Khng th ci t ng dng. Vui lng th li sau.",
        variant: "destructive"
      });
    } finally {
      setIsInstalling(false);
    }
  };

  /**
   * ng banner
   */
  const handleCloseBanner = () => {
    setShowBanner(false);
    // Lu vo localStorage  khng hin th li trong session ny
    localStorage.setItem('pwa-banner-dismissed', Date.now().toString());
  };

  // Kim tra xem banner  b dismiss cha
  useEffect(() => {
    const dismissed = localStorage.getItem('pwa-banner-dismissed');
    if (dismissed) {
      const dismissedTime = parseInt(dismissed);
      const now = Date.now();
      // Hin th li banner sau 24 gi
      if (now - dismissedTime < 24 * 60 * 60 * 1000) {
        setShowBanner(false);
        return;
      }
    }
  }, []);

  // Khng hin th g nu khng th ci t
  if (!canInstall) {
    return null;
  }

  return (
    <>
      {/* Install Banner */}
      {showBanner && (
        <div className="fixed bottom-4 left-4 right-4 z-50 md:left-auto md:right-4 md:max-w-sm">
          <Card className="shadow-lg border-blue-200 bg-gradient-to-r from-blue-50 to-indigo-50">
            <CardHeader className="pb-3">
              <div className="flex items-start justify-between">
                <div className="flex items-center space-x-2">
                  <Smartphone className="w-5 h-5 text-blue-600" />
                  <CardTitle className="text-sm font-semibold text-gray-900">
                    Ci t ng dng Yapee
                  </CardTitle>
                </div>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={handleCloseBanner}
                  className="h-6 w-6 p-0 hover:bg-gray-200"
                >
                  <X className="w-4 h-4" />
                </Button>
              </div>
              <CardDescription className="text-xs text-gray-600">
                Tri nghim mua sm nhanh hn vi ng dng di ng
              </CardDescription>
            </CardHeader>
            <CardContent className="pt-0">
              <div className="flex space-x-2">
                <Button
                  onClick={handleInstall}
                  disabled={isInstalling}
                  size="sm"
                  className="flex-1 bg-blue-600 hover:bg-blue-700"
                >
                  <Download className="w-4 h-4 mr-1" />
                  {isInstalling ? 'ang ci t...' : 'Ci t'}
                </Button>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleCloseBanner}
                  className="text-gray-600"
                >
                   sau
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Install Button in Header/Menu */}
      {!showBanner && (
        <Button
          variant="ghost"
          size="sm"
          onClick={handleInstall}
          disabled={isInstalling}
          className="hidden md:flex items-center space-x-1 text-blue-600 hover:text-blue-700 hover:bg-blue-50"
        >
          <Download className="w-4 h-4" />
          <span className="text-sm">Ci t App</span>
        </Button>
      )}
    </>
  );
};

export default InstallPWA;