import React, { useState, useEffect } from 'react';
import { RefreshCw, X, AlertCircle } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { pwaService } from '@/services/pwaService';
import { useToast } from '@/hooks/use-toast';

/**
 * Component thng bo cp nht PWA
 */
const PWAUpdateNotification: React.FC = () => {
  const [updateAvailable, setUpdateAvailable] = useState(false);
  const [isUpdating, setIsUpdating] = useState(false);
  const [showNotification, setShowNotification] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    // Lng nghe s kin c cp nht mi
    const handleUpdateAvailable = () => {
      setUpdateAvailable(true);
      setShowNotification(true);
      
      toast({
        title: "Cp nht mi c sn!",
        description: "Phin bn mi ca Yapee  sn sng  ci t.",
      });
    };

    // ng k event listener cho service worker
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.addEventListener('message', (event) => {
        if (event.data && event.data.type === 'UPDATE_AVAILABLE') {
          handleUpdateAvailable();
        }
      });
    }

    // Kim tra cp nht khi component mount
    checkForUpdates();

    return () => {
      // Cleanup event listeners nu cn
    };
  }, [toast]);

  /**
   * Kim tra cp nht
   */
  const checkForUpdates = async () => {
    try {
      if ('serviceWorker' in navigator) {
        const registration = await navigator.serviceWorker.getRegistration();
        if (registration) {
          await registration.update();
        }
      }
    } catch (error) {
      console.error('Li khi kim tra cp nht:', error);
    }
  };

  /**
   * p dng cp nht
   */
  const handleUpdate = async () => {
    setIsUpdating(true);
    
    try {
      await pwaService.applyUpdate();
      
      toast({
        title: "Cp nht thnh cng!",
        description: "ng dng s c ti li  p dng phin bn mi.",
      });
      
      // Reload trang sau 1 giy
      setTimeout(() => {
        window.location.reload();
      }, 1000);
      
    } catch (error) {
      console.error('Li khi cp nht:', error);
      toast({
        title: "Li cp nht",
        description: "Khng th cp nht ng dng. Vui lng th li sau.",
        variant: "destructive"
      });
      setIsUpdating(false);
    }
  };

  /**
   * ng thng bo
   */
  const handleClose = () => {
    setShowNotification(false);
  };

  /**
   * Hon cp nht
   */
  const handlePostpone = () => {
    setShowNotification(false);
    // Hin th li thng bo sau 30 pht
    setTimeout(() => {
      if (updateAvailable) {
        setShowNotification(true);
      }
    }, 30 * 60 * 1000);
  };

  // Khng hin th g nu khng c cp nht
  if (!updateAvailable || !showNotification) {
    return null;
  }

  return (
    <div className="fixed top-4 left-4 right-4 z-50 md:left-auto md:right-4 md:max-w-sm">
      <Card className="shadow-lg border-orange-200 bg-gradient-to-r from-orange-50 to-yellow-50">
        <CardHeader className="pb-3">
          <div className="flex items-start justify-between">
            <div className="flex items-center space-x-2">
              <AlertCircle className="w-5 h-5 text-orange-600" />
              <CardTitle className="text-sm font-semibold text-gray-900">
                Cp nht mi c sn
              </CardTitle>
            </div>
            <Button
              variant="ghost"
              size="sm"
              onClick={handleClose}
              className="h-6 w-6 p-0 hover:bg-gray-200"
            >
              <X className="w-4 h-4" />
            </Button>
          </div>
          <CardDescription className="text-xs text-gray-600">
            Phin bn mi ca Yapee vi nhiu tnh nng v ci tin
          </CardDescription>
        </CardHeader>
        <CardContent className="pt-0">
          <div className="space-y-2">
            <Alert className="border-orange-200 bg-orange-50">
              <AlertDescription className="text-xs text-orange-800">
                Cp nht ngay  tri nghim cc tnh nng mi nht!
              </AlertDescription>
            </Alert>
            
            <div className="flex space-x-2">
              <Button
                onClick={handleUpdate}
                disabled={isUpdating}
                size="sm"
                className="flex-1 bg-orange-600 hover:bg-orange-700"
              >
                <RefreshCw className={`w-4 h-4 mr-1 ${isUpdating ? 'animate-spin' : ''}`} />
                {isUpdating ? 'ang cp nht...' : 'Cp nht ngay'}
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={handlePostpone}
                className="text-gray-600"
                disabled={isUpdating}
              >
                 sau
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default PWAUpdateNotification;