import React, { useState, useEffect } from 'react';

/**
 * Hook  debounce mt gi tr
 * @param value - Gi tr cn debounce
 * @param delay - Thi gian delay (ms)
 * @returns Gi tr  c debounce
 */
export function useDebounce<T>(value: T, delay: number): T {
  const [debouncedValue, setDebouncedValue] = useState<T>(value);

  useEffect(() => {
    // To timer  update gi tr sau khi delay
    const handler = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    // Cleanup function  clear timer nu value thay i trc khi delay kt thc
    return () => {
      clearTimeout(handler);
    };
  }, [value, delay]);

  return debouncedValue;
}

export default useDebounce;